:root {
  --font-YoungSerif: "Young Serif", sans-serif;
  --font-Outfit: "Outfit", sans-serif;
}

:root {
  --nutmeg: hsl(14, 45%, 36%);
  --darkRaspberry: hsl(332, 51%, 32%);
  --white: hsl(0, 0%, 100%);
  --roseWhite: hsl(330, 100%, 98%);
  --egshell: hsl(30, 54%, 90%);
  --grey: hsl(75, 2%, 51%);
  --lightGrey: hsl(30, 18%, 87%);
  --wengeBrown: hsl(30, 10%, 34%);
  --darkCharcoal: hsl(24, 5%, 18%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  max-width: 1440px;
  max-height: 1955px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-Outfit);
  background-color: var(--egshell);
}

html,
body {
  height: 100%;
  display: flex;
  justify-content: center;
}
@media (min-width: 40em) {
  html,
body {
    margin-top: 2.8125rem;
  }
}

h1,
h2,
h3,
h4 {
  margin: 0;
  line-height: 1.1;
  font-family: var(--font-YoungSerif);
  color: var(--nutmeg);
  font-size: 1.5rem;
}

ul {
  margin: 0.875rem 0 0 0;
  padding: 0 0.875rem;
  list-style: square;
}

li {
  text-align: start;
  color: var(--grey);
  font-size: 1rem;
  padding: 0.5rem 0.75rem;
}

li::marker {
  color: var(--darkRaspberry);
  font-size: 0.75rem;
}

ol {
  padding: 0 0.875rem;
}
ol li::marker {
  font-weight: 700;
  font-size: 1rem;
}

span {
  font-weight: 700;
  color: var(--wengeBrown);
}

p {
  font-size: 1rem;
  font-weight: 400;
  color: var(--grey);
  line-height: 1.5;
}

.numbers {
  color: var(--darkRaspberry);
  font-weight: 700;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.wrapper .content {
  max-width: 45.3125rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: var(--white);
}
@media (min-width: 40em) {
  .wrapper .content {
    padding: 1.5rem;
    border-radius: 1.25rem;
  }
}
.wrapper .content__recipie-introduction--image {
  max-width: 40.9375rem;
  max-height: 18.75rem;
}
@media (min-width: 40em) {
  .wrapper .content__recipie-introduction--image {
    margin: 0 auto;
  }
}
.wrapper .content__recipie-introduction--image img {
  display: block;
  width: 100%;
  height: auto;
}
@media (min-width: 40em) {
  .wrapper .content__recipie-introduction--image img {
    border-radius: 0.75rem;
  }
}
.wrapper .content__recipie-introduction--text {
  padding: 1.5rem 1.875rem 0 1.875rem;
  text-align: start;
}
@media (min-width: 71.875em) {
  .wrapper .content__recipie-introduction--text {
    padding: 1.5rem 1.25rem;
  }
}
.wrapper .content__recipie-introduction--text h1 {
  font-size: 1.875rem;
  color: var(--darkCharcoal);
}
.wrapper .content__recipie-introduction--preperationTime {
  margin: 0 1.5rem 1.5rem 1.5rem;
  background-color: var(--roseWhite);
  padding: 1.5rem;
  text-align: start;
  border-radius: 0.75rem;
}
.wrapper .content__recipie-introduction--preperationTime p {
  color: var(--darkRaspberry);
  font-size: 1.125rem;
  font-weight: 700;
  margin: 0;
}
.wrapper .content__recipie-ingredients {
  margin-inline: 1.5rem;
  padding: 1.5rem 0;
  border-bottom: 1px solid var(--lightGrey);
}
.wrapper .content__recipie-instructions {
  margin: 1.5rem;
  border-bottom: 1px solid var(--lightGrey);
}
.wrapper .content__recipie-nutritions {
  margin: 0 2.1875rem 1.5rem 2.1875rem;
}
@media (min-width: 40em) {
  .wrapper .content__recipie-nutritions {
    margin-bottom: 0;
    margin-inline: 1.5rem;
  }
}
.wrapper .content__recipie-nutritions table {
  width: 100%;
  font-family: var(--font-Outfit);
}
.wrapper .content__recipie-nutritions table tr {
  text-align: left;
}
.wrapper .content__recipie-nutritions table tr td {
  padding: 0.5rem 1.5625rem;
  font-size: 1rem;
  font-weight: 400;
  color: var(--grey);
  border-bottom: 1px solid var(--lightGrey);
}
.wrapper .content__recipie-nutritions table :nth-child(2) {
  color: var(--darkRaspberry);
  font-weight: 700;
  padding: 0.5rem 1.5625rem;
}
.wrapper .content__recipie-nutritions table :nth-child(4) td {
  border-bottom: none;
}
.wrapper footer {
  margin-top: 0.625rem;
}
.wrapper footer .attribution {
  font-size: 12px;
  text-align: center;
}
@media (min-width: 40em) {
  .wrapper footer .attribution {
    font-size: 16px;
  }
}
.wrapper footer .attribution a {
  color: #3e52a3;
}